import { Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { Employee } from '../EmployeeInterface';
import { LoginserviceService } from '../loginservice.service';
import { BsModalService,BsModalRef } from 'ngx-bootstrap/modal';
import { DialogService } from '../dialog.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  empdata:any[]=[];
  p:number=1;


  empobj:Employee={
    id:0,
    name:'',
    department:'',
    status:'',
    mobileNo:0,
    country:{
      cid:0,
      cname:''
    },
    createddtm:0,
    createdby:'',
    updateddtm:0,
    updatedby:'',

  };

  isradioCheck:boolean=false;

  modalRef: BsModalRef; 

  config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-success"
  };

  nameSearch:String='';
  
  constructor(private router:Router, 
    private service:LoginserviceService, 
    private modalservice:BsModalService,
    private dialogservice:DialogService) { }

  ngOnInit() {

    this.getAlldata();
  }

  getAlldata()
{
  this.service.getAlldata()
 .subscribe((response)=>{
   console.log(response);
   this.empdata=(<any> response);  
 })
}
  onAddEmployee()
  {
      this.router.navigate(['/addEmployee']);
     
  }
  onRadio(item)
  {
    console.log("Radio Button Work:"+item);
    this.empobj=item;
    console.log(this.empobj);
    this.isradioCheck=true;
  }

  isRadioCheck():boolean
  {
      if(this.isradioCheck==true)
      {
        return true;
      }
      else
      {
        return false;
      }
  }

  onUpdateEmployee(popup: TemplateRef<any>)
  {
    console.log(this.empobj);
    if(this.isRadioCheck())
    {
      this.modalRef=this.modalservice.show(popup,this.config);
    }else
    {
      alert("Please Select The Employee To Update.");
    }
  }

  onDelete()
  {
    if(this.isRadioCheck())
    {
      this.dialogservice.OpenConfirmDialog('Are you sure to delete this record?')
      .afterClosed()
      .subscribe((res)=>{
        console.log(res);
        if(res){
          this.service.deleteEmployee(this.empobj.id)
          .subscribe((response)=>{
           console.log(response);
           this.getAlldata();
          //console.log(res);
          
        })
      }
        else
        {
          console.log(res);
        }

      });
    }else
    {
      alert("Please Select The Employee To Delete.");
    }

  }

  onChangeStatus()
  {
    let objStatus={
              
      id:this.empobj.id,
      status:this.empobj.status
    }

    this.service.changeStatus(objStatus)
    .subscribe((response)=>{
     console.log(response);
     
    })
  }
}